export default {
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			assemble:false,
			ass_time:false,
			show: false,
			value: '',
			currentIndex: 0,
			text_box: [{
				name: '秒杀活动'
			}, {
				name: '拼团活动'
			},{
				name: '刮刮活动'
			},{
				name: '直接获取'
			}],
			date: '请选择开始日期',
			date1: '请选择结束日期',
			num:'',
			num1:'',
			number:'',
			mander:'',
			member:'',
			rarea_el:'',
		}
	},
	onLoad() {
		console.log(uni.getStorageSync("data"));
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	methods: {
		blur(){
			this.num = parseInt(this.number * this.num1)
		},
		blur1(){
			this.num = parseInt(this.number * this.num1)
		},
		//验证
		payment(){
			if(this.value == ''){
				uni.showModal({
				title: '温馨提示',
					content: '请选择券的类型',
					showCancel: false
				});
				return
			}
			if(this.currentIndex == 2 || this.currentIndex == 3){
				if(this.num == ''){
					uni.showModal({
						title: '温馨提示',
						content: '请填写发券数量',
						showCancel: false
					});
					return
				}
				this.paymoney()
			}
			if(this.currentIndex == 0){
				if(this.num == ''){
					uni.showModal({
						title: '温馨提示',
						content: '请填写发券数量',
						showCancel: false
					});
					return
				}
				this.verifct()
				this.paymoney()
			}
			if(this.currentIndex == 1){
				if(this.num == '' || this.num1 == '' || this.number == '' || this.mander == '' || this.member == '' || this.rarea_el == ''){
					uni.showModal({
						title: '温馨提示',
						content: '拼团数据没有填写完整',
						showCancel: false
					});
					return
				}
				this.verifct()
				this.paymoney()
			}
		},
		//数据对应
		changeIndex(index) {
			this.currentIndex = index;
			this.value = this.text_box[index].name
			this.show = !this.show
			if(index == 0){
				this.assemble = false
				this.ass_time = true
			}
			if(index == 1){
				this.assemble = true
				this.ass_time = true
			}
			if(index == 2){
				this.assemble = false
				this.ass_time = false
			}
			if(index == 3){
				this.assemble = false
				this.ass_time = false
			}
		},
		//时间判断
		verifct(){
			if(isNaN(Date.parse(this.date)) == true || isNaN(Date.parse(this.date1)) == true){
				uni.showModal({
					title: '时间不能为空',
					content: '请重新选择',
					showCancel: false
				});
				return
			}
			if(this.date==this.date1){
				uni.showModal({
					title: '有效时间不能低于一天',
					content: '请重新选择',
					showCancel: false
				});
				return
			}
			if(this.date>this.date1){
				uni.showModal({
					title: '结束时间不能小于开始时间',
					content: '请重新选择',
					showCancel: false
				});
				return
			}
		},
		//支付
		paymoney(){
			console.log(123)
		},
		open_list: function() {
			this.show = !this.show
		},
		bindDateChange: function(e) {
			this.date = e.target.value
		},
		bindDateChange1: function(e) {
			this.date1 = e.target.value
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();
		
			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
	}
}
